<template>
	<v-dialog
			v-model="dialogDetail"
			fullscreen
			hide-overlay
			transition="dialog-bottom-transition"
	>
		<template v-slot:activator="{ on }">
			<v-flex v-on="on">
				<v-icon small>settings</v-icon>
				<span class="ml-1">
					
				</span>

			</v-flex>

		</template>
		<v-card>
			<v-toolbar dark color="primary">
				<v-btn icon dark @click="closeDialog()">
					<v-icon>close</v-icon>
				</v-btn>
				<v-toolbar-title> </v-toolbar-title>
				<v-spacer></v-spacer>
				<v-toolbar-items>
					<v-btn dark flat @click="sendUserData()"></v-btn>
				</v-toolbar-items>
			</v-toolbar>
			<v-container grid-list-md fluid>
				<v-form v-if="!getProcessing">
					<v-card>
						<v-card-title class="title">
							 
						</v-card-title>
						<v-card-text>
							<v-layout wrap>
								<v-flex xs9>
									<v-card
											class="mb-3"
											v-for="(detail, i) in componentDetails"
											:key="`detail-${detail.name + i}`"
									>
										<v-card-text>
											<v-layout>
												<v-flex xs10>
													<v-layout wrap>
														<v-flex xs4>
															<v-text-field
																	label="  *"
																	required
																	hide-details
																	:value="detail.name"
																	@change="detail.name = $event"
															></v-text-field>
														</v-flex>
														<v-flex xs1>
															<v-text-field
																	label=" *"
																	hide-details
																	:value="detail.width"
																	@change="detail.width = Number($event)"
																	required
																	type="number"
															></v-text-field>
														</v-flex>
														<v-flex xs1>
															<v-text-field
																	label=" *"
																	hide-details
																	:value="detail.height"
																	@change="detail.height = Number($event)"
																	required
																	type="number"
															></v-text-field>
														</v-flex>
														<v-flex align-self-end xs2>
															<p class="mb-0 mr-3 text-xs-right">:</p>
														</v-flex>
														<v-flex xs1
																v-for="indent in detail.indents"
																:key="'name' + indent.name"
														>
															<v-text-field
																	:label="indent.name"
																	hide-details
																	:value="indent.value"
																	@change="indent.value = Number($event)"
																	required
																	type="number"
															></v-text-field>
														</v-flex>
													</v-layout>
												</v-flex>
												<v-flex xs2 align-self-end class="text-xs-right">
													<v-btn
															small
															color="red"
															@click="removeDetail(i)"
															:disabled="componentDetails.length <= 1"
													>
														
													</v-btn>
												</v-flex>

											</v-layout>
										</v-card-text>
									</v-card>
								</v-flex>
								<v-flex xs9 class="text-xs-center">
									<v-btn
											color="green"
											@click="pushDetail()"
									>
										  
									</v-btn>
								</v-flex>
							</v-layout>

						</v-card-text>
					</v-card>
				</v-form>
			</v-container>
		</v-card>
	</v-dialog>
</template>
<script>
	import {mapGetters, mapActions} from 'vuex'
	const defaultData = () => {
		return {
			detail: {
				name: '',
				width: null,
				height: null,
				indents: {
					top: {
						name: '',
						value: 2
					},
					right: {
						name: '',
						value: 2
					},
					bottom: {
						name: '',
						value: 2
					},
					left: {
						name: '',
						value: 2
					}
				}
			}
		}
	}
	export default {
		name: 'DetailsFormCard',
		data: () => ({
			dialogDetail: false,
			componentDetails: [],
		}),
		computed: {
			...mapGetters([
				'getProcessing',
				'getDetails'
			])
		},
		methods: {
			inputHandler(event, data) {
				console.log(data)
				console.log(event)
			},
			sendUserData() {
				let sendData = {
					details: this.componentDetails
				}
				this.$store.dispatch('sendUserData', sendData)
				this.dialogDetail = false
			},
			closeDialog() {
				this.setComponentDetails(this.details)
				this.dialogDetail = false
			},
			removeIndent(index) {
				this.indents.splice(index, 1)
			},
			setComponentDetails(payload){
				this.componentDetails = [...payload]
			},
			pushDetail() {
				this.componentDetails.push(defaultData().detail)
			},
			removeDetail(index) {
				this.componentDetails.splice(index, 1)
			}
		},
		mounted() {
			this.setComponentDetails(this.getDetails)
		},
		watch: {
			getDetails(payload) {
				this.setComponentDetails(payload)
			}
		},
		props: {
			details: {
				default: () => {
					return [defaultData().detail]
				},
				type: Array
			}
		}
	}
</script>

