<template>
	<div
			class="detail"
			:style="detailStyle"
			@click="click($event)"
			@focus="$emit('focus', $event)"
	>
<!--		<v-btn flat icon>
			<v-icon>rotate_90_degrees_ccw</v-icon>
		</v-btn>-->
	</div>
</template>

<script>
	export default {
		name: "Detail",
		props: {
			colorDetail: {
				default: '#4CAF50',
				type: String
			},
			colorIndent: {
				default: '#1B5E20',
				type: String
			},
			width: {
				default: 1,
				type: Number
			},
			height: {
				default: 1,
				type: Number
			},
			borderBottomWidth: {
				default: 2,
				type: Number
			},
			borderTopWidth: {
				default: 2,
				type: Number
			},
			borderLeftWidth: {
				default: 2,
				type: Number
			},
			borderRightWidth: {
				default: 2,
				type: Number
			},
		},
		computed: {
			detailStyle() {
				return {
					minWidth: this.width + 'px',
					minHeight: this.height + 'px',
					width: this.width + 'px',
					height: this.height + 'px',
					borderBottomWidth: this.borderBottomWidth + 'px',
					borderTopWidth: this.borderTopWidth + 'px',
					borderLeftWidth: this.borderLeftWidth + 'px',
					borderRightWidth: this.borderRightWidth + 'px',
					borderColor: this.colorIndent,
					backgroundColor: this.colorDetail
				}
			}
		},
		methods: {
			click(e) {
				this.$emit('click', e)
				this.detailStyle.width = this.height + 'px'
				this.detailStyle.height = this.width + 'px'
			},
			detailFocus() {
				console.log(this);
				return ''
			}
		}
	}
</script>

