<template>
	<v-dialog
			v-model="dialogScrim"
			fullscreen
			hide-overlay
			transition="dialog-bottom-transition"
	>
		<template v-slot:activator="{ on }">
			<v-flex v-on="on">
				<v-icon small>settings</v-icon>
				<span class="ml-1">
					
				</span>

			</v-flex>

		</template>
		<v-card>
			<v-toolbar dark color="primary">
				<v-btn icon dark @click="closeDialog()">
					<v-icon>close</v-icon>
				</v-btn>
				<v-toolbar-title>      </v-toolbar-title>
				<v-spacer></v-spacer>
				<v-toolbar-items>
					<v-btn dark flat @click="sendUserData()"></v-btn>
				</v-toolbar-items>
			</v-toolbar>
			<v-container grid-list-md fluid>
				<v-form v-if="!getProcessing">
					<v-card class="mb-3">
						<v-card-title class="title">
							    
						</v-card-title>
						<v-card-text>
							<v-layout wrap>
								<v-flex xs9>
									<v-card class="pl-3 mb-3"
											v-for="(scrimIndent, i) in indents"
											:key="`indent-${scrimIndent.name+i}`"
									>
										<v-layout>
											<v-flex grow>
												<v-text-field
														label="  *"
														required
														:value="scrimIndent.name"
														@change="scrimIndent.name = $event"
												></v-text-field>
											</v-flex>
											<v-flex>
												<v-text-field
														label=" *"
														required
														:value="scrimIndent.values.left"
														@change="scrimIndent.values.left = Number($event)"
														type="number"
												></v-text-field>
											</v-flex>
											<v-flex>
												<v-text-field
														label=" *"
														required
														:value="scrimIndent.values.top"
														@change="scrimIndent.values.top = Number($event)"
														type="number"
												></v-text-field>
											</v-flex>
											<v-flex>
												<v-text-field
														label=" *"
														required
														:value="scrimIndent.values.right"
														@change="scrimIndent.values.right = Number($event)"
														type="number"
												></v-text-field>
											</v-flex>
											<v-flex>
												<v-text-field
														label=" *"
														required
														:value="scrimIndent.values.bottom"
														@change="scrimIndent.values.bottom = Number($event)"
														type="number"
												></v-text-field>
											</v-flex>
											<v-flex grow align-self-center class="text-xs-center">
												<v-btn
														small
														color="red"
														@click="removeIndent(i)"
														:disabled="indents.length <= 1"
												>
													
												</v-btn>
											</v-flex>
										</v-layout>
									</v-card>
								</v-flex>
								<v-flex xs9 class="text-xs-center">
									<v-btn
											color="green"
											@click="pushIndent()"
									>
										 
									</v-btn>
								</v-flex>
							</v-layout>
						</v-card-text>
					</v-card>
					<v-card>
						<v-card-title class="title">
							 
						</v-card-title>
						<v-card-text>
							<v-layout wrap>
								<v-flex xs9>
									<v-card
											class="mb-3"
											v-for="(scrim, i) in componentScrims"
											:key="`scrim-${scrim.name + i}`"
									>
										<v-card-text>
											<v-layout>
												<v-flex xs10>
													<v-layout wrap>
														<v-flex xs4>
															<v-text-field
																	label="  *"
																	required
																	hide-details
																	:value="scrim.name"
																	@change="scrim.name = $event"
															></v-text-field>
														</v-flex>
														<v-flex xs4>
															<v-text-field
																	label=" *"
																	hide-details
																	:value="scrim.width"
																	@change="scrim.width = Number($event)"
																	required
																	type="number"
															></v-text-field>
														</v-flex>
														<v-flex xs4>
															<v-text-field
																	label=" *"
																	hide-details
																	:value="scrim.height"
																	@change="scrim.height = Number($event)"
																	required
																	type="number"
															></v-text-field>
														</v-flex>
													</v-layout>
												</v-flex>
												<v-flex xs2 align-self-end class="text-xs-right">
													<v-btn
															small
															color="red"
															@click="removeScrim(i)"
															:disabled="componentScrims.length <= 1"
													>
														
													</v-btn>
												</v-flex>

											</v-layout>
										</v-card-text>
									</v-card>
								</v-flex>
								<v-flex xs9 class="text-xs-center">
									<v-btn
											color="green"
											@click="pushScrim()"
									>
										 
									</v-btn>
								</v-flex>
							</v-layout>

						</v-card-text>
					</v-card>
				</v-form>
			</v-container>
		</v-card>
	</v-dialog>
</template>
<script>
	import {mapGetters, mapActions} from 'vuex'
	const defaultData = () => {
		return {
			indent: {
				name: '',
				values: {
					top: null,
					right: null,
					bottom: null,
					left: null
				}
			},
			scrim: {
				name: '',
				width: null,
				height: null
			}
		}
	}
	export default {
		name: 'ScrimsFormCard',
		data: () => ({
			dialogScrim: false,
			indents: [],
			defaultIndent: {
				name: '',
				values: {
					top: null,
					right: null,
					bottom: null,
					left: null
				}
			},
			componentScrims: [],
			defaultScrim: {
				name: '',
				width: null,
				height: null
			}
		}),
		computed: {
			...mapGetters([
				'getProcessing',
				'getScrims'
			])
		},
		methods: {
			inputHandler(event, data) {
				console.log(data)
				console.log(event)
			},
			sendUserData() {
				let sendData = {
					scrimIndents: this.indents,
					scrims: this.componentScrims
				}
				this.$store.dispatch('sendUserData', sendData)
				this.dialogScrim = false
			},
			closeDialog() {
				this.setThisIndents(this.scrimIndents)
				this.setComponentScrims(this.scrims)
				this.dialogScrim = false
			},
			setThisIndents(payload){
				this.indents = [...payload]
			},
			pushIndent() {
				this.indents.push(defaultData().indent)
			},
			removeIndent(index) {
				this.indents.splice(index, 1)
			},
			setComponentScrims(payload){
				this.componentScrims = [...payload]
			},
			pushScrim() {
				this.componentScrims.push(defaultData().scrim)
			},
			removeScrim(index) {
				this.componentScrims.splice(index, 1)
			}
		},
		watch: {
			getScrims() {
				this.setComponentScrims(this.scrims)
				this.setThisIndents(this.scrimIndents)
			}
		},
		props: {
			scrims: {
				default: () => {
					return [this.defaultScrim]
				},
				type: Array
			},
			scrimIndents: {
				default: () => {
					return [this.defaultIndent]
				},
				type: Array
			},
		}
	}
</script>

<style scoped>

</style>