<script>
  import Detail from '../components/Detail'
  import swatches from 'vue-color/src/components/Swatches'
  import { mapActions, mapGetters } from 'vuex'
  import ScrimWithDetail from '../plugins/scrimWithDetail/scrimWithDetail';
  import ScrimsFormCard from '../components/ScrimsFormCard';
  import DetailsFormCard from '../components/DetailsFormCard';

  export default {
    data: () => ({
      dialogDetails: false,
      dark: true,
      drawers: ['Default (no property)', 'Permanent', 'Temporary'],
      indentsLock : false,
      primaryDrawer: {
        model: null,
        type: 'default (no property)',
        clipped: false,
        floating: false,
        mini: false,
      },
      footer: {
        inset: false
      },
      right: null,
      n: 0
    }),
    methods: {
      ...mapActions([
        'setDetailSizes',
        'setDetailHeight',
        'setDetailWidth',

        'setIndent',
        'setIndents',

        'setScrimSizes',
        'setScrimWidth',
        'setScrimHeight',
        'changeScrimOffset',

        'setColorDetail',
        'setColorIndent',
        'setColorScrimIndent',
        'setColorContentBackground',

        'reloadDetailsInScrim',
        'createBaseDetails',
        'createRotatedDetails',
        'setDetailDirection',
        'setShowRotated',
        'setScrimIndent',

      ]),
      changeIndentsLock() {
        console.log(this.n)
        ++this.n
        this.indentsLock = !this.indentsLock
      },
      getDetailsTotalInScrimSize(scrimWidth, scrimHeight) {
        let offsetWidth = this.scrimIndent.values.left+this.scrimIndent.values.top
        let offsetHeight = this.scrimIndent.values.left+this.scrimIndent.values.bottom

        const details = new ScrimWithDetail({
          scrimWidth: scrimWidth,
          scrimHeight: scrimHeight,

          offsetWidth: offsetWidth,
          offsetHeight: offsetHeight,

          detailWidth: this.detail.width,
          detailHeight: this.detail.height,

          detailIndentLeft: this.detail.indents.left.value,
          detailIndentTop: this.detail.indents.top.value,
          detailIndentRight: this.detail.indents.right.value,
          detailIndentBottom: this.detail.indents.bottom.value,

          showRotated: this.showRotated
        })
        const totalDetails = details[this.detailDirection]
        return totalDetails.scrim.totalCountDetails
      },
    },
    computed: {
      ...mapGetters([
        'getUserAuth',
        'getProcessing',

        'detail',
        'details',
        'scrim',
        'scrimIndents',
        'scrims',
        'indents',
        'baseDetails',
        'rotatedDetails',
        'showRotated',
        'scrimIndent',

        // 'createBaseDetails',
        'getShortDetailsInScrimShortTotal',
        'getRotatedDetailCount',
        'getDetails',

        'colorDetail',
        'colorIndent',
        'colorScrimIndent',
        'colorContentBackground',
        'getDetailsInScrim',

        'detailDirection',
      ]),
      //TODO  
      minDetailSize() {
        if (this.detail.width <= 1
            || this.detail.width === ''
            || !this.detail.width
            || this.detail.width === String) {
          return 1
        }
        if (this.detail.height < 1) {
          return 1
        }
        return ''
      },
      getRotatedDetailsStyle() {
        let rotatedDetail = this.getDetailsInScrim.details.rotatedDetail

        let countInModShortSide = this.getDetailsInScrim.details.rotatedDetail.countInModShortSide
        let countInModLongSide = this.getDetailsInScrim.details.rotatedDetail.countInModLongSide

        let rotatedLongSide = this.getDetailsInScrim.details.rotatedDetail.atLongSide
        let rotatedShortSide = this.getDetailsInScrim.details.rotatedDetail.atShortSide

        let style = {}

        if (rotatedLongSide) {
          style = {
            minWidth: (countInModLongSide + 1) * rotatedDetail.width + 'px',
          }
        }
        if (rotatedShortSide) {
          style = {
            minWidth: (rotatedShortSide + 1) * rotatedDetail.width + 'px',
          }
        }
        return style
      },

      getBaseDetailsStyle() {
        //     
        let style = {}
        let rotatedModAtLongSide = this.getDetailsInScrim.details.rotatedDetail.countInModLongSide
        if (!rotatedModAtLongSide) {
          style = {
            minWidth:
                this.getDetailsInScrim.scrim.shortSide +
                this.getDetailsInScrim.details.baseDetail.width + 'px'
          }
        } else {
          style = {
            minWidth:
                this.getDetailsInScrim.details.baseDetail.atShortSide *
                this.getDetailsInScrim.details.baseDetail.width + 'px'
          }
        }
        return style
      },

      scrimStyles() {
        return {
          width: this.getDetailsInScrim.scrim.shortSide + 'px',
          height: this.getDetailsInScrim.scrim.longSide + 'px',

          borderTopWidth: this.scrimIndent.values.top + 'px',
          borderRightWidth: this.scrimIndent.values.right + 'px',
          borderBottomWidth: this.scrimIndent.values.bottom + 'px',
          borderLeftWidth: this.scrimIndent.values.left + 'px',
          borderColor: this.colorScrimIndent.hex,
        }
      },

      totalDetailWidth() {
        const detailWidth = Number(this.detail.width)
        const detailPaddingLeft = Number(this.indents.left.value)
        const detailPaddingRight = Number(this.indents.right.value)
        return detailWidth + detailPaddingLeft + detailPaddingRight
      },
      totalDetailHeight() {
        const detailHeight = Number(this.detail.height)
        const detailPaddingTop = Number(this.indents.top.value)
        const detailPaddingBottom = Number(this.indents.bottom.value)
        return detailHeight + detailPaddingTop + detailPaddingBottom
      }
    },
    created() {
      this.reloadDetailsInScrim()
      // this.scrimIndent = this.scrimIndents[0]
    },
    watch: {
      // scrimIndents(indents){
      //   this.scrimIndent = indents[0]
      //   this.changeScrimOffset(indents[0])
      // },
      detail() {
        this.reloadDetailsInScrim()
      },
      scrim() {
        this.reloadDetailsInScrim()
      },
    },
    components: {
      DetailsFormCard,
      ScrimsFormCard,
      Detail,
      'swatches-picker': swatches
    },
  }
</script>
